<template>
  <div class="register">
    <div class="content">
      <div @click="goBack">X</div>
      <!-- logo -->
      <div class="header">
        <img src="@/assets/logo-home.png" />
      </div>
      <!--  -->
      <div class="main">
        <wInput
          type="text"
          maxlength="11"
          placeholder="/"
          v-model="phoneData"
        ></wInput>
        <wInput
          type="password"
          maxlength="11"
          placeholder=""
          v-model="passData"
        ></wInput>
        <wInput
          type="number"
          maxlength="4"
          placeholder="1234"
          isShowCode
          ref="codeRef"
          v-model="verCode"
          @setCode="getVerCode()"
        ></wInput>
      </div>

      <wButton
        text=" "
        :rotate="isRotate"
        @click.native="startReg()"
      ></wButton>

      <!--  -->
      <div class="footer">
        <span
          @tap="isShowAgree"
          class="cuIcon"
          :class="showAgree ? 'cuIcon-radiobox' : 'cuIcon-round'"
          ></span
        >
        <!--  -->
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import wInput from "@/components/watch-login/watch-input.vue";
import wButton from "@/components/watch-login/watch-button.vue";
import { register } from "@/api/login.js";
import { Toast } from "vant";

export default {
  data() {
    return {
      phoneData: "", // /
      passData: "", //
      verCode: "", //
      showAgree: true, //
      isRotate: false //
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    isShowAgree() {
      //
      this.showAgree = !this.showAgree;
    },
    getVerCode() {
      //
      if (this.phoneData.length != 11) {
        Toast("");
        return false;
      }
      this.$refs.runCode.$emit("runCode"); //
      setTimeout(() => {
        this.$refs.runCode.$emit("runCode", 0); // 
      }, 60000);
    },
    async startReg() {
      //
      if (this.isRotate) {
        //
        return false;
      }
      if (!this.showAgree) {
        Toast("");
        return false;
      }
      if (this.phoneData.length != 11) {
        Toast("");
        return false;
      }
      if (this.passData.length < 6) {
        Toast("");
        return false;
      }
      if (this.verCode !== "1234") {
        Toast("");
        return false;
      }
      this.isRotate = true;
      try {
        await register({ userName: this.phoneData, password: this.passData });
        Toast("");
        this.isRotate = false;
        this.goBack();
      } catch (error) {
        this.isRotate = false;
      }
    }
  },
  components: {
    wInput,
    wButton
  }
};
</script>

<style>
@import url("../components/watch-login/css/icon.css");
@import url("../assets/css/main.css");
</style>
